body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.overlay {
  background-color: #000;
  opacity: 0.5;
  position: absolute;
  height: calc(100vh - 56px); /* navbar height */
  width: 100%;
  left: 0;
  right: 0;
}

.homeDisplayText {
  position: absolute;
  color: #ffffff;
  z-index: 1;
  left: 10vw;
  top: 30vh;
}

.homeImage {
  height: calc(100vh - 56px); /* navbar height */
  width: 100%;
  object-fit: cover;
  z-index: -2;
}

.profileImage {
  width: 30vw;
  height: 30vw;
  object-fit: cover;
  border-radius: 50%;
}

.divider {
  height: 5px;
  background-color: lightwhitesmoke;
  width: 100%;
}

.flexbox {
  width: 100%;
  text-align: center;
  margin-bottom: 7vh;
}

.workCard {
  width: "18rem";
}

.workCard:hover {
  box-shadow: 10px 10px 20px gray;
}

.researchCard {
  width: "18rem";
}

.researchCard:hover {
  box-shadow: 10px 10px 20px gray;
}

